body {
    font-family: Arial, sans-serif;
 }
 
 form {
   display: inline-flex;
   flex-direction: column;
 }
 
 input[type="text"], input[type="email"], select {
    padding: 14px 20px;
    margin: 4%;
    border-radius: 2%;
    font-size: 2vh;
 }

 select{
   height: 40px;
 }

 textarea{
   padding: 14px 20px;
    margin: 4%;
    border-radius: 2%;
    font-size: 2vh;
    height:100px;
 }
 
 input[type="submit"] {
    color: white;
    padding: 14px 20px;
    border: none;
    cursor: pointer;
    height: 100px;
 }
 
 input[type="submit"]:hover {
    opacity: 0.8;
 }
 
 #error {
    color: red;
    margin-left: 2%;
 }